body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(202, 202, 202, 0.411);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.result{
  margin-top: 70px;
}
.card{
  margin: 10px;
  padding: 10px;
  text-align: center;
  width: 20%;
  border-radius: 10px;
  background-color: hsl(36, 94%, 49%);
}
.profile-pic{
  padding-top: 10px; 
  width: 110px;
  height : 110px;
  border-radius: 50%;
  margin: none;
}
.container{
  margin-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}
.card:hover{
  width: 25%;
  transition: 1s;
  box-shadow: 15px 15px 15px hsla(306, 99%, 49%, 0.982);
  border-radius: 10%;
}
button:hover{
  box-shadow: 10px 10px 10px hsla(212, 70%, 51%, 0.982);
  background-color: rgb(110, 184, 234);
  border-radius: 15px;
  transition: 0.5s;
}
button{
  width:250px;
  height: 50px;
  background-color: rgba(6, 147, 255, 0.874);
  border-radius: 10px;
  font-family: 'Times New Roman', Times, serif;
  font-size: large;
}
.coin{
  color: rgb(0, 0, 0);
  font-family:'Times New Roman', Times, serif;
  background-color: rgb(196, 196, 196);
  height: 200px;
  width: 200px;
  border-radius: 50%;
  text-align: center;
  margin-top: 50px;
}